import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { EndpointsService } from './endpoints.service';

import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { LoadingModule } from 'ngx-loading';
import { jqxSchedulerComponent } from 'jqwidgets-scripts/jqwidgets-ts/angular_jqxscheduler';
import { jqxComboBoxComponent } from 'jqwidgets-scripts/jqwidgets-ts/angular_jqxcombobox';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AuthguardService } from './service/authguard.service';
import { HttpClientModule } from '@angular/common/http';

import {CalendarModule} from "ap-angular2-fullcalendar";
import { ColorPickerModule } from 'ngx-color-picker';



// shared components/pipes/services structure ... https://github.com/housseindjirdeh/angular2-hn
// lazy shared structure ... https://github.com/ngx-translate/core/issues/209
import { SharedModule } from './shared/shared.module';
import { ServicesModule } from './shared/services/services.module';
import { SharedComponentsModule } from './shared/components/shared-components.module';
import { GestionclasesComponent } from './gestionclases/gestionclases.component';
import { ProgramarClaseComponent } from './programar-clase/programar-clase.component';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { EjerciciosComponent } from './ejercicios/ejercicios.component';
import { RutinaComponent } from './rutina/rutina.component';
import { AsignarComponent } from './asignar/asignar.component';
import { ProfesoresComponent } from './profesores/profesores.component';
import { MaquinasComponent } from './maquinas/maquinas.component';
import { NovedadesComponent } from './novedades/novedades.component';
import { SortPipe } from './sort.pipe';
import { LoginComponent } from './login/login.component';
import { OpinionesComponent } from './opiniones/opiniones.component';
import { CentroComponent } from './centro/centro.component';
import { VentasComponent } from './ventas/ventas.component';

// shared pipes module

@NgModule({
  declarations: [

    AppComponent,
    GestionclasesComponent,
    ProgramarClaseComponent,
    UsuariosComponent,
    EjerciciosComponent,
    RutinaComponent,
    AsignarComponent,
    ProfesoresComponent,
    MaquinasComponent,
    NovedadesComponent,
    jqxSchedulerComponent,
    jqxComboBoxComponent,
    SortPipe,
    LoginComponent,
    OpinionesComponent,
    CentroComponent,
    VentasComponent

  ],
  imports: [
 CalendarModule,
 ColorPickerModule,
  LoadingModule,
  NgxDatatableModule,
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
        FormsModule,
        ReactiveFormsModule,
    SharedModule,
    ServicesModule.forRoot(),
    SharedComponentsModule,
  ],
  providers: [
  EndpointsService,
  AuthguardService,
    {
      provide: LocationStrategy,
      useClass: HashLocationStrategy
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



